.Reasons {
    padding: 0 2rem;
    display: flex;
    gap: 2rem;
}

.left-r {
    flex: 1;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 1rem;
    grid-auto-rows: 1fr;
}

.left-r>img {
    object-fit: cover;
}

.left-r>:nth-child(1) {
    width: 12rem;
    grid-row: 1/3;
    height: 36rem;
}

.left-r>:nth-child(2) {
    width: 25rem;
    grid-column: 2/4;
    height: 14rem;
}

.left-r>:nth-child(3) {
    width: 14rem;
    grid-column: 2/3;
    grid-row: 2;
    height: 7rem;
}

.left-r>:nth-child(4) {
    width: 10rem;
    grid-row: 2;
    grid-column: 3/4;
    height: 16rem;
}

.right-r {
    flex: 1;
    text-transform: uppercase;
    display: flex;
    flex-direction: column;
    justify-content: center;
    gap: 1rem;
}

.right-r>span {
    font-weight: bold;
    color: var(--orange);
}

.right-r>div {
    color: white;
    font-size: 3rem;
    font-weight: bold;
}

.details-r {
    display: flex;
    flex-direction: column;
    gap: 1rem;
}

.details-r>div {
    display: flex;
    font-size: 1rem;
    gap: 1rem;
}

.details-r>div>img {
    width: 2rem;
    height: 2rem;
}

.details-r>div>span {
    font-weight: bold;
    font-size: 1rem;
}

.details-r:nth-last-child(0) {
    display: flex;
    flex-direction: row;
    gap: 1rem;
}

@media screen and (max-width:768px) {
    .Reasons {
        flex-direction: column;
    }

    .left-r {
        grid-auto-rows: auto;
        overflow: hidden;
    }

    .left-r>:nth-child(1) {
        width: 7rem;
        height: 17rem;
    }

    .left-r>:nth-child(2) {
        width: 15rem;
        height: 8.5rem;
    }

    .left-r>:nth-child(3) {
        width: 9rem;
    }

    .left-r>:nth-child(4) {
        width: 3rem;
        height: 5rem;
    }

    .right-r>div>span:nth-child(2) {
        font-size: 35px;
    }
}